.App {
  text-align: center;
}

.App-logo {
  height: 45px;/*40vmin 에서 이미지 사이즈 줄임 */
  pointer-events: none;
}

.App-header {
  background-color: #02b875;
  min-height: 50px; /*전체 사이즈를 100vh에서 줄임*/
  padding: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


